<template>
  <div class="container my-3">
    <div class="d-flex justify-content-between align-items-center">
      <h2 class="border-bottom py-2">ÏßàÎ¨∏ Î™©Î°ù</h2>
      <router-link
        v-if="isAuthenticated"
        to="/questions/new"
        class="btn btn-primary btn-sm"
      >
        ÏßàÎ¨∏ ÏûëÏÑ±ÌïòÍ∏∞
      </router-link>
    </div>

    <table class="table">
      <thead class="table-dark">
        <tr class="text-center">
          <th>Î≤àÌò∏</th>
          <th style="width: 50%">Ï†úÎ™©</th>
          <th>Í∏ÄÏì¥Ïù¥</th>
          <th>ÏûëÏÑ±ÏùºÏãú</th>
        </tr>
      </thead>
      <tbody>
        <tr
          v-for="question in questions"
          :key="question.id"
          class="text-center"
        >
          <td>{{ question.id }}</td>
          <td class="text-start">
            <router-link :to="`/questions/${question.id}`" class="custom-link">
              {{ question.subject }}
              <span v-if="question.commentCount > 0" class="comment-count"
                >[{{ question.commentCount }}]</span
              >
            </router-link>
          </td>
          <td>{{ question.author?.username || "ÏùµÎ™Ö" }}</td>
          <td>{{ formatDate(question.createDate) }}</td>
        </tr>
      </tbody>
    </table>

    <!-- ÌéòÏù¥Ïßï Ïª®Ìä∏Î°§ -->
    <div class="d-flex justify-content-center">
      <button
        @click="prevPage"
        :disabled="page === 0"
        class="btn btn-sm btn-secondary mx-1"
      >
        Ïù¥Ï†Ñ
      </button>
      <span class="mx-2">ÌéòÏù¥ÏßÄ {{ page + 1 }} / {{ totalPages }}</span>
      <button
        @click="nextPage"
        :disabled="page >= totalPages.value - 1"
        class="btn btn-sm btn-secondary mx-1"
      >
        Îã§Ïùå
      </button>
    </div>

    <!-- Í≤ÄÏÉâ ÏûÖÎ†•Ï∞Ω -->
    <div class="input-group mt-3">
      <input
        v-model="searchKeyword"
        @keyup.enter="fetchQuestions"
        type="text"
        class="form-control"
        placeholder="Í≤ÄÏÉâÏñ¥Î•º ÏûÖÎ†•ÌïòÏÑ∏Ïöî(Ï†úÎ™© & ÎÇ¥Ïö©(ÏßàÎ¨∏/ÎãµÎ≥Ä))"
      />
      <button @click="fetchQuestions" class="btn btn-outline-secondary">
        Í≤ÄÏÉâ
      </button>
    </div>
  </div>
</template>

<script>
import { ref, computed, onMounted } from "vue";
import axios from "@/api";
import dayjs from "dayjs";
import { useUserStore } from "@/store/user";

export default {
  setup() {
    const userStore = useUserStore();
    const questions = ref([]);
    const page = ref(0);
    const pageSize = ref(10);
    const totalElements = ref(0);
    const searchKeyword = ref("");

    const isAuthenticated = computed(() => !!userStore.currentUser);

    const fetchCommentCount = async (questionId) => {
      try {
        const response = await axios.get(`/questions/${questionId}/answers`);
        return response.data.length;
      } catch (error) {
        console.error(`ÎåìÍ∏Ä Ïàò Í∞ÄÏ†∏Ïò§Í∏∞ Ïã§Ìå® (ÏßàÎ¨∏ ID: ${questionId}):`, error);
        return 0;
      }
    };

    const fetchQuestions = async () => {
      try {
        const response = await axios.get(
          `/questions?page=${page.value}&kw=${searchKeyword.value}`,
          { withCredentials: true }
        );
        const questionList = response.data.content;

        const questionsWithCommentCount = await Promise.all(
          questionList.map(async (q) => ({
            ...q,
            commentCount: await fetchCommentCount(q.id),
          }))
        );

        questions.value = questionsWithCommentCount;
        totalElements.value = response.data.totalElements || 0;
      } catch (error) {
        console.error("ÏßàÎ¨∏ Î™©Î°ù Î∂àÎü¨Ïò§Í∏∞ Ïã§Ìå®:", error);
      }
    };

    const totalPages = computed(() =>
      Math.ceil(totalElements.value / pageSize.value)
    );

    const prevPage = () => {
      if (page.value > 0) {
        page.value--;
        fetchQuestions();
      }
    };

    const nextPage = () => {
      if (page.value < totalPages.value - 1) {
        page.value++;
        fetchQuestions();
      }
    };

    onMounted(async () => {
      await userStore.fetchUser();
      fetchQuestions();
    });

    return {
      questions,
      page,
      pageSize,
      totalElements,
      searchKeyword,
      isAuthenticated,
      totalPages,
      fetchQuestions,
      prevPage,
      nextPage,
      formatDate: (dateString) =>
        dayjs(dateString).format("YYYY-MM-DD HH:mm:ss"),
    };
  },
};
</script>

<style scoped>
/* Ï†úÎ™© ÎßÅÌÅ¨ Í∏∞Î≥∏: Í≤ÄÏ†ïÏÉâ + Ïñ∏ÎçîÎùºÏù∏ ÏóÜÏùå */
.custom-link {
  color: black;
  text-decoration: none;
  transition: color 0.2s;
}

/* ÎßàÏö∞Ïä§ Ïò§Î≤Ñ Ïãú: ÌååÎûÄÏÉâÏúºÎ°ú ÏÉâÏÉÅ Î≥ÄÍ≤Ω */
.custom-link:hover {
  color: #007bff;
  text-decoration: none;
}

/* ÎåìÍ∏Ä Ïàò Ïä§ÌÉÄÏùº: ÌöåÏÉâ + Ìïú Ïπ∏ ÎùÑÏõÄ */
.comment-count {
  color: gray;
  margin-left: 5px;
}
</style>
